.card {
  height: 46.875rem;
  width: 100%;
  max-width: 31.25rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  padding: 2.5rem;
  background: var(--very-dark-blue-card-bg);
  border-radius: 1.25rem;
}
.card .container {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 0.625rem;
}
.card__title {
  cursor: pointer;
  transition: 150ms ease-in-out;
}
.card__title:hover {
  color: var(--cyan);
}
.card__img {
  position: relative;
  height: 380px;
  width: 100%;
  max-width: 420px;
  border-radius: 16px;
  overflow: hidden;
  cursor: pointer;
  transition: background-image 150ms ease;
}
.card__img::before {
  content: url("./../../images/icon-view.jpg") !important;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 16px;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  opacity: 0;
  transition: opacity 150ms ease-in-out;
  z-index: 2;
}
.card__img:hover {
  content: url("./../../images/icon-view.jpg") !important;
  opacity: 0.5;
  background-color: cyan;
}
.card .ethereum {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: var(--cyan);
  gap: 0.625rem;
  grid-column: 1;
}
.card .time--left {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: var(--soft-blue);
  gap: 0.625rem;
  grid-column: 4;
}
.card__avatar {
  height: 50px;
  border: 2px solid var(--white);
  border-radius: 999px;
}
@media (max-width: 35em) {
  .card__img {
    max-height: 40.625rem;
    height: auto;
  }
  .card .container {
    display: grid;
    grid-template-columns: 0.1fr 99fr 2fr;
    grid-template-rows: 0.1fr -0.1fr;
  }
  .card__avatar {
    grid-column: 1;
  }
  .card .container p {
    grid-column: 2;
    grid-row: 1;
  }
  .card .adjustments {
    grid-column: 2;
    grid-row: 2;
    margin-top: -1.25rem;
  }
}

:root {
  --soft-blue: hsl(215, 51%, 70%);
  --cyan: hsl(178, 100%, 50%);
  --very-dark-blue: hsl(217, 54%, 11%);
  --very-dark-blue-card-bg: hsl(216, 50%, 16%);
  --very-dark-blue-card-line: hsl(215, 32%, 27%);
  --white: hsl(0, 0%, 100%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  font-size: 1.125rem;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: 0;
  padding: 1.25rem;
  background-color: var(--very-dark-blue);
  font-family: "Outfit", sans-serif;
  min-height: 150vh;
}

main {
  display: flex;
  justify-content: center;
  margin-bottom: 1.25rem;
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 0;
  color: var(--white);
}

p {
  color: var(--soft-blue);
  font-weight: 300;
}

.attribution {
  font-size: 18px;
  text-align: center;
  color: var(--soft-blue);
}

a {
  text-decoration: none;
  color: var(--white);
  transition: 150ms ease-in-out;
}

a:hover {
  color: var(--cyan);
}

hr {
  border: none; /* Remove any border */
  border-top: 2px solid var(--very-dark-blue-card-line); /* Add a solid line */
  background: none; /* Remove any background */
  margin: 1em 0; /* Add some vertical spacing */
  width: 100%;
  max-width: 26.25rem;
}

.separate {
  display: grid;
  grid-template-columns: 2fr 1fr 2.5fr 2fr;
}

.adjustments {
  margin-left: -5px;
}